import {combineReducers} from "redux"

import {commonReducer, CommonEntry} from './common'
import {mallReducer, MallEntry} from './mall'
import {treasureForumReducer, TreasureForumEntry} from './treasure_forum'
import {museumReducer, MuseumEntry} from './museum'
import {xiaomeiReducer, XiaomeiEntry} from './xiaomei'
import {lxtReducer, LxtEntry} from './lxt'


const rootReducer = combineReducers({
    commonReducer,
    mallReducer,
    treasureForumReducer,
    museumReducer,
    xiaomeiReducer,
    lxtReducer
});

export default rootReducer

export interface entries {
    mallReducer: MallEntry,
    commonReducer: CommonEntry,
    treasureForumReducer: TreasureForumEntry,
    museumReducer: MuseumEntry,
    xiaomeiReducer: XiaomeiEntry,
    lxtReducer: LxtEntry
}
