import {treasureForum} from 'apis'
import * as commonActions from './common'
import {encryptByDes} from 'utils'
import {Toast} from 'antd-mobile'

export const Types = {
    getTreasure: 'GET_TREASURE',
    changeTreasureData: 'CHANGE_TREASURE_DATA',
    getTreasureTypes: 'GET_TREASURE_TYPES'
}

const changeTreasureData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeTreasureData,
        payload: data
    })
}

const getTreasure = (obj: object) => async (dispatch: any) => {
    const {list} = await commonActions.getPaginationData(obj)(dispatch)
    dispatch({
        type: Types.getTreasure,
        payload: {
            list
        }
    })
}

const getTreasureTypes = (obj: object) => async (dispatch: any) => {
    const {list} = await treasureForum.getTreasureTypes()
    dispatch({
        type: Types.getTreasureTypes,
        payload: {
            list
        }
    })
    return {list}
}

const praiseTreasure = (sayId: string) => async (dispatch: any) => {
    const {isOk} = await treasureForum.praiseTreasure({sayId})
    if (isOk) {
        Toast.success('点赞成功！')
    }
    return {isOk}
}

const attentionTreasure = (userIds: string[], isAttention: number) => async (dispatch: any) => {
    let userId = ''
    userIds.map(e => {
        userId += encryptByDes(e, 'header')
    })
    const {isOk} = await treasureForum.attentionTreasure({userId, isAttention})
    if (isOk) {
        Toast.success((!!isAttention ? '取消' : '') + '关注用户成功！')
    }
    return {isOk}
}

const collectTreasure = (sayId: string, status: number) => async (dispatch: any) => {
    const {isOk} = await treasureForum.collectTreasure({sayId, status})
    if (isOk) {
        Toast.success((!!status ? '' : '取消') + '收藏成功！')
    }
    return {isOk}
}

const getTreasureDetail = (sayId: string) => async (dispatch: any) => {
    const treasure = await treasureForum.getTreasureDetail({sayId})
    dispatch({
        type: Types.changeTreasureData,
        payload: {
            treasure
        }
    })
    return treasure
}

export {
    praiseTreasure,
    collectTreasure,
    attentionTreasure,
    getTreasure,
    changeTreasureData,
    getTreasureTypes,
    getTreasureDetail
}
