import {commonApis} from 'apis'
import {localDB, Utils, WxConfig, Dialog, encryptByDes} from 'utils'
import {Toast} from 'antd-mobile'

export const Types = {
    selectTabBar: 'SELECT_TABBAR',
    getGoodsCategories: 'GET_GOODS_CATEGORIES',
    changeCommonData: 'CHANGE_COMMON_DATA',
    getPaginationData: 'GET_PAGINATION_DATA',
    getStoreInfo: 'GET_STORE_INFO',
    getComments: 'GET_COMMENTS',
    loginSuccess: 'LOGIN_SUCCESS',
    getCities: "GET_CITIES",
    getSubCities: 'GET_SUB_CITIES',
    toggleModal: 'TOGGLE_MODAL'
}

const selectTabBar = (index: number) => async (dispatch: any) => {
    await dispatch({
        type: Types.selectTabBar,
        payload: {
            index
        }
    })
}

const getGoodsCategories = () => async (dispatch: any) => {
    const {goodsCategories} = await commonApis.getGoodsCategories()
    await dispatch({
        type: Types.getGoodsCategories,
        payload: {
            list: goodsCategories
        }
    })
    return {goodsCategories}
}
const getPaginationData = (obj: object): any => async (dispatch: any) => {
    Toast.loading('数据加载中...')
    await dispatch({
        type: Types.changeCommonData,
        payload: {loadMore: true}
    })
    const {totalPage, list, data} = await commonApis.getPaginationData(obj)
    await dispatch({
        type: Types.getPaginationData,
        payload: {
            totalPage
        }
    })
    Toast.hide()
    await dispatch({
        type: Types.changeCommonData,
        payload: {loadMore: false}
    })
    return {list, data}
}

const changeCommonData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeCommonData,
        payload: data
    })
    return data
}


const getComments = (data: any) => async (dispatch: any) => {
    const {storeId} = data
    const {list} = await getPaginationData({
        url: storeId ? '/queryStoreComment' : '/queryFixedReview',
        data
    })(dispatch)
    await dispatch({
        type: Types.getComments,
        payload: {
            list
        }
    })
    return {comments: list}
}

const login = (data: any) => async (dispatch: any) => {
    const {name} = data
    let obj = {}
    if (name === '13246683556' || name === '13714204799' || name === '18825216402') {
        obj = {
            role: 'root',
            name
        }
    }
    let userInfo = await commonApis.login(data)
    userInfo.tkCipherText = userInfo.tk
    userInfo.tk = encryptByDes(userInfo.tk, 'header')
    Object.assign(userInfo, obj)
    await dispatch({
        type: Types.loginSuccess,
        payload: {
            userInfo
        }
    })
    await localDB.DBSetter('userInfo', userInfo)
    Toast.success('账号登录成功！')
    return userInfo
}

const getWxConfig = (data = {}) => async (dispatch: any) => {
    const {appId, nonceStr, signature, timestamp, ...userInfo} = await commonApis.getWxConfig(data)
    const wxConfig = {
        appId, timestamp, nonceStr, signature
    }
    await dispatch({
        type: Types.loginSuccess,
        payload: {
            userInfo
        }
    })
    await dispatch({
        type: Types.changeCommonData,
        payload: {
            wxConfig
        }
    })
    await localDB.DBSetter('userInfo', userInfo)
    await localDB.DBSetter('wxConfig', wxConfig)
    return {wxConfig}
}

const wxOAuth2 = () => async (dispatch: any) => {
    const {search, href, origin, pathname} = window.location
    // const wx_Config = JSON.parse(localDB.DBGetter('wxConfig') || '{}')
    // if (!!wx_Config.appId) {
    //     await WxConfig.config(wx_Config)
    //     return
    // }
    const notWX = navigator.userAgent.toLowerCase().indexOf('micromessenger') === -1;
    if(notWX){
        Dialog.Confirm({
            msg: '非微信浏览器内无法进行授权登录,请复制链接在微信浏览器内打开!',
        })
        return
    }
    const {tk} = JSON.parse(localDB.DBGetter('userInfo') || "{}")
    if (!!tk) return
    const {code, state} = Utils.getParamsBySearch(search)
    if (!!code && state === 'authorized') {
        const path = href.split('#')[1]
        const {wxConfig} = await getWxConfig({
            code,
            url: href.split('#')[0]
        })(dispatch)
        await WxConfig.config(wxConfig)
        return
    }
    Dialog.Confirm({
        msg: '微信授权登录可获取更好的体验，是否马上去授权？',
        onConfirm: WxConfig.getWxCode
    })
}

const getUserStore = () => async (dispatch: any) => {
    const storeInfo = await commonApis.getUserStore()
    await dispatch({
        type: Types.getStoreInfo,
        payload: {
            storeInfo
        }
    })
}

const getUploadToken = () => async (dispatch: any) => {
    const qiniuToken = await commonApis.getUploadToken()
    await dispatch({
        type: Types.changeCommonData,
        payload: {
            qiniuToken
        }
    })
    await localDB.DBSetter('qiniuToken', qiniuToken)
    return qiniuToken
}


const uploadQiniu = (data: any) => async (dispatch: any) => {
    const res = await commonApis.uploadQiniu(data)
    // await dispatch({
    //     type: Types.changeCommonData,
    //     payload: {
    //         qiniuToken
    //     }
    // })
    return res
}

const getCities = (type: string = '0') => async (dispatch: any) => {
    const {cityList = []} = await commonApis.getCities(type)
    cityList.map((e: any) => {
        e.value = e.number
        e.label = e.name
        return e
    })
    await dispatch({
        type: Types.getCities,
        payload: {
            list: cityList
        }
    })
    return {cityList}
}

const getSubCities = (cities: any[]) => async (dispatch: any) => {
    const number = [...cities].pop()
    const {cityList = []} = await commonApis.getSubCities(number)
    cityList.map((e: any) => {
        e.value = e.number
        e.label = e.name
        return e
    })
    await dispatch({
        type: Types.getSubCities,
        payload: {
            cities,
            list: cityList
        }
    })
    return {cityList, cities}
}

const getMyMuseums = (territory: number) => async (dispatch: any) => {
    const {museumList = []} = await commonApis.getMyMuseum()
    await dispatch({
        type: Types.changeCommonData,
        payload: {
            myMuseums: museumList
        }
    })
    return {list: museumList}
}

const toggleModal = () => async (dispatch: any) => {
    await dispatch({
        type: Types.toggleModal
    })
}

export {
    selectTabBar,
    getGoodsCategories,
    getPaginationData,
    changeCommonData,
    getComments,
    login,
    getWxConfig,
    wxOAuth2,
    getUserStore,
    getUploadToken,
    uploadQiniu,
    getCities,
    getSubCities,
    getMyMuseums,
    toggleModal
}
