import {ActionType} from 'action'
import {Types} from 'action/museum'

export const initState: MuseumEntry = {
    museumList: [],
    adsInfoList: [],
    museum: {},
    collection: {},
    museumType: {},
    museumSubType: {},
    adsInfoDetail: {},
    collectionList: [],
    museumTypes: [],
    museumSubTypes: [],
    collectionComments: []
};

export interface MuseumEntry {
    museum: any,
    museumType: any,
    adsInfoDetail: any,
    collection: any,
    museumSubType: any,
    museumList: any[],
    adsInfoList: any[],
    museumTypes: any[],
    museumSubTypes: any[],
    collectionList: any[],
    collectionComments: any[]
}

export function museumReducer(
    state = initState,
    action: ActionType
) {
    const {
        type, payload = {}
    } = action
    const {
        adsInfoList,
        museumList,
        collectionList,
        museumTypes,
        museumSubTypes,
        collectionComments
    } = state
    const {
        list = [],
        museum
    } = payload
    switch (type) {
        case Types.getMuseums:
            state.museumList = [...museumList, ...list]
            break;
        case Types.getAdsInfos:
            state.adsInfoList = [...adsInfoList, ...list]
            break;
        case Types.getCollections:
            state.collectionList = [...collectionList, ...list]
            break;
        case Types.getMuseumTypes:
            state.museumTypes = [...museumTypes, ...list]
            break;
        case Types.getMuseumSubTypes:
            state.museumSubTypes = [...museumSubTypes, ...list]
            break;
        case Types.getCollectionComments:
            state.collectionComments = [...collectionComments, ...list]
            break;
        case Types.getMuseumDetail:
            state.museum = museum
            break;
        case Types.changeMuseumData:
            Object.assign(state, payload)
            break;
        default:
            break;
    }
    return {...state}
}

