import {lxtApis} from 'apis'

export const Types = {
    changeLxtData: 'CHANGE_LXT_DATA',
}

const changeLxtData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeLxtData,
        payload: data
    })
}
const getNoticeInfoDetail = (id: string) => async (dispatch: any) => {
    const noticeInfoDetail = await lxtApis.getNoticeInfoDetail({id})
    await dispatch({
        type: Types.changeLxtData,
        payload: {noticeInfoDetail}
    })
    return noticeInfoDetail
}


export {
    changeLxtData,
    getNoticeInfoDetail
}

