import React from 'react'

import './index.scss'
import {entries} from "../../reducer";
import {connect} from "react-redux";
import {withRouter} from "react-router";

interface P {
    className?: string,
    loadMore: boolean
}

interface S {
}

class EmptyData extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                loadMore
            },
            state: {}
        } = this
        return (
            !loadMore && <div className={`empty_data ${className}`}>
              <img src="/lsc_h5_hash/imgs/nodata-search.png" alt="" />
              <div>暂无数据</div>
            </div>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {loadMore} = state.commonReducer
    return {
        loadMore
    }
}

export default connect(
    mapStateToProps
)(withRouter(EmptyData))
