import React, {ReactNode} from 'react'
import {List, Picker} from 'antd-mobile'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {commonActions} from 'action'
import {entries} from 'reducer'
import './index.scss'

interface P {
    className?: string,
    pacArr: any[],
    getSubCities: any,
    getCities: any,
    changeCommonData: any,
    formFields: any,
    children: ReactNode,
    title?: string,
    name?: string,
    onChange?: any,
    props?: any
}

interface S {
}


class AsyncPca extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                formFields,
                name = 'pca',
                className = '',
                children,
                pacArr,
                title = '区域选择',
                props = {}
            },
            state: {},
            onOk,
            onVisibleChange,
            onPickerChange
        } = this
        return (
            <Picker title={title} data={pacArr} onOk={onOk} value={formFields[name] || []}
                    onVisibleChange={onVisibleChange} onPickerChange={onPickerChange} {...props}>
                {
                    !!children ? <div>{children}</div> :
                        <List.Item className={`async_pca ${className}`} arrow="horizontal">{title}</List.Item>
                }
            </Picker>
        );
    }

    onOk = async (v: any) => {
        const {
            onChange = () => {
            },
            changeCommonData,
            formFields,
            name = 'pca'
        } = this.props
        const [province, city, area] = v
        formFields[name] = v
        Object.assign(formFields, {province, city, area})
        await changeCommonData({formFields: {...formFields}})
        await onChange(v)
    }
    onChange = async (v: any) => {
    }

    onPickerChange = async (v: any) => {
        const {getSubCities, changeCommonData, name = 'pca', formFields} = this.props
        if (v.length < 3) {
            await getSubCities(v)
        }
        formFields[name] = v
        await changeCommonData({formFields: {...formFields}})
    }

    onVisibleChange = async (bool: boolean) => {
        const {pacArr, getCities, formFields: {territory}} = this.props
        if (bool && !pacArr.length) {
            await getCities(territory)
        }
    }

    componentDidMount(): void {

    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {pacArr, formFields} = state.commonReducer
    return {pacArr, formFields}
}

function mapDispatchToProps(dispatch: any) {
    const {getSubCities, getCities, changeCommonData} = commonActions
    return {
        ...bindActionCreators({getCities, getSubCities, changeCommonData}, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AsyncPca)
