import React from 'react'
import {MuseumItem, EmptyData} from 'components'

import './index.scss'

interface P {
    className?: string,
    list: any[],
    showStyle?: string
}

interface S {
}


class MuseumList extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                list,
                showStyle
            },
            state: {}
        } = this
        return (
            !list.length ? <EmptyData /> :
                <div className={`museum_list ${showStyle === 'distance' ? 'distance' : 'grid'}`}>
                    {
                        list.map((e: any, i: number) => <MuseumItem showStyle={showStyle} key={i} museum={e} />)
                    }
                </div>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default MuseumList
