import React, {ReactNode} from 'react'
import {createForm, formShape} from 'rc-form';
import {InputItem, SearchBar, Stepper, TextareaItem, Picker, List} from 'antd-mobile'
import {entries} from "reducer";
import {commonActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";

import './index.scss'

export interface P {
    className?: string,
    auto?: boolean,
    fieldType?: string,
    name: string,
    length?: number,
    defaultValue?: any,
    optionProps?: any,
    placeholder?: any,
    onChange?: any,
    props?: any,
    form: any,
    changeCommonData: any,
    formFields: any,
    options?: any[]
}

class VsonField extends React.Component<P> {

    static propTypes = {
        form: formShape
    }

    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                options = [],
                formFields,
                name = 'field',
                fieldType = 'input',
                placeholder,
                defaultValue,
                length = 300,
                optionProps = {},
                props = {},
                form: {
                    getFieldProps
                }
            },
            onFieldChange,
            onBlur
        } = this
        const {
            auto = false,
            rule = {rules: [{required: true}]},
            rows = 8
        } = props

        const {
            label = 'text',
            value = 'value'
        } = optionProps

        const allProps = {
            ...getFieldProps(name, rule),
            onChange: onFieldChange,
            placeholder,
            className: `vson_field ${className}`,
            value: formFields[name] !== undefined ? formFields[name] : defaultValue,
            ...props
        }
        let node: any = null
        switch (fieldType) {
            case "search":
                allProps.onChange = (v: any) => onFieldChange(v, 'composing')
                allProps.onSubmit = (v: any) => onFieldChange(v)
                node = <SearchBar clear={true} {...allProps} />
                break;
            case "select":
                let arr: any[] = []
                options.map((e: any) => {
                    arr.push({
                        ...e,
                        label: e[label],
                        value: e[value]
                    })
                })
                const styles = allProps.className + ' vson_select'
                delete allProps.className
                allProps.value = [allProps.value]
                node = <Picker cols={1}  {...allProps} data={arr}>
                    <List.Item className={styles} arrow="horizontal">{props.title}</List.Item>
                </Picker>
                break;
            case "stepper":
                allProps.className += ' vson_stepper'
                node = <Stepper showNumber={true} {...allProps} />
                break;
            case "textarea":
                // clear={true} autoHeight={true}
                node = <TextareaItem onBlur={onBlur} rows={rows} {...allProps} autoHeight={true} count={length} />
                break
            default:
                if (auto) {
                    allProps.autofocus = 'autofocus'
                }
                node = <InputItem onBlur={onBlur}  {...allProps} />
                break
        }
        return (node);
    }

    onBlur = () => {
        const {length} = document.getElementsByClassName('vson_scroll')
        if (!!length) {
            setTimeout(() => {
                window.scrollTo(0, 0)
            }, 100)
        }
    }

    onFieldChange = async (val: any, f?: any) => {
        const {
            props: {
                onChange = () => {
                }
            },
            reSetVal
        } = this
        await reSetVal(val)
        if (f !== 'composing') {
            await onChange(val)
        }
    }

    reSetVal = async (val: any) => {
        const {
            changeCommonData, formFields, name, fieldType = 'input'
        } = this.props
        let v: any;
        switch (fieldType) {
            case "select":
                v = val[0]
                break;
            default:
                v = val
                break
        }
        formFields[name] = v
        await changeCommonData({formFields: {...formFields}})
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }

    componentWillReceiveProps(nextProps: Readonly<P>, nextContext: any): void {
        const {options: n = []} = nextProps
        const {options = [], defaultValue} = this.props
        if (!!options.length && !n.length) {
            this.reSetVal(defaultValue)
        }
    }
}


function mapStateToProps(state: entries) {
    const {formFields} = state.commonReducer
    return {
        formFields
    }
}

function mapDispatchToProps(dispatch: any) {
    const {changeCommonData} = commonActions
    return {
        ...bindActionCreators({changeCommonData}, dispatch),
        dispatch
    };
}

export default createForm()(connect(
    mapStateToProps,
    mapDispatchToProps
)(VsonField))

// export default createForm()(VsonField)
