import React from 'react'

import './index.scss'

export interface P {
    className?: string,
    collection: any
}

class CollectionDetail extends React.Component<P> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            collection: {
                type,
                size = '',
                place = '',
                era = '',
                weight = '',
                weightUnit = '',
                objectDetImg = [],
                objectVideo = []
            }, className = ''
        } = this.props
        return (
            <div className={`${className} goods_detail_component padding_ud`}>
                <div className={'padding_lr infos'}>
                    <div className={'header'}>详情</div>
                    <div className={'info padding'}>【{type === '7' ? "型号" : "年代"}】{era}</div>
                    <div className={'info padding'}>【尺寸】{size}</div>
                    <div className={'info padding'}>【重量】{weight}({weightUnit})</div>
                    <div className={'info padding'}>【产地】{place}</div>
                </div>
                <div className={'imgs'}>
                    {
                        objectDetImg.map((e: any, i: number) =>
                            <img className={'margin_u lazy_img'} alt='' src={'/lsc_h5_hash/imgs/logo_bg.png'}
                                 data-src={e.big || e.medium}
                                 key={i} />
                        )
                    }
                </div>
                <div className={'videos'}>
                    {
                        objectVideo.map((e: any, i: number) =>
                            <video className={'margin_u'} controls src={e.video} key={i} />
                        )
                    }
                </div>
            </div>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default CollectionDetail
