import React from 'react';
import {BrowserRouter, HashRouter, Switch, Route, Redirect} from "react-router-dom";
import {entries} from "reducer";
import {commonActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {createBrowserHistory, createHashHistory} from 'history'
import {Home, Register, Mall, Museums, XiaoMeiIntelligent, Lxt} from 'pages';
import {localDB} from 'utils'
import './App.scss';

const history = createBrowserHistory()
export {
    history
}

window.onpagehide = () => {
    localDB.DBRemove('userInfo')
}
window.onpopstate = function (e: any) {
    // console.log('后退',e)
}
history.listen(async (location: any, action) => {
    if (location.href && location.href.includes('/xiaomei_intelligent/')) {
        document.title = '小美智能'
    } else {
        document.title = '乐收藏'
    }
    // console.log(location, action)
    // if (!window.location.href.includes('?location=replace')) {
    //     window.location.href = window.location.href + '?location=replace'
    // }
    // window.location.assign(location.href)
})

interface P {
    wxOAuth2: any,
    history: any,
    fromWebview: boolean
}

interface S {

}

class App extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {},
            state: {}
        } = this
        return (
            // Route   exact strict
            // HOST=apis.myvsoncloud.com
            // forceRefresh basename={'/lsc_h5'}
            /*
            * 切换路由模式需要修改以下内容
            *   .env.production PUBLIC_URL
            *   node_mudules/react-script/config/path.js
            *       appBuild的值
            * */
            // <BrowserRouter basename='/lsc_h5'>
            <HashRouter>
                <Switch>
                    {/*path={["/home/gallery", "/home/museum", "/home/mall_home","/home/treasure_forum", "/home/mine"]}*/}
                    <Route path="/home/:sub" strict exact component={Home} />
                    <Route path="/register" component={Register} />
                    {
                        Object.entries(Mall).map(([pageName, {page, params = ''}], i) => (
                            <Route key={i} path={'/mall/' + pageName + params} component={page} />
                        ))
                    }
                    {
                        Object.entries(Museums).map(([pageName, {page, params = ''}], i) => (
                            <Route key={i} path={'/museums/' + pageName + params} component={page} />
                        ))
                    }
                    {
                        Object.entries(XiaoMeiIntelligent).map(([pageName, {page, params = ''}], i) => (
                            <Route key={i} path={'/xiaomei_intelligent/' + pageName + params} component={page} />
                        ))
                    }
                    {
                        Object.entries(Lxt).map(([pageName, {page, params = ''}], i) => (
                            <Route key={i} path={'/lxt/' + pageName + params} component={page} />
                        ))
                    }
                    <Redirect to="/home/mall_home" />
                </Switch>
            </HashRouter>
            // </BrowserRouter>
        );
    }

    componentDidMount(): void {
        const {wxOAuth2, fromWebview: f} = this.props
        const hashs = window.location.hash.split('/')
        const pathName = hashs[2]
        const p4 = hashs[6]
        const fromWebview = f || p4 === 'fromWebview'
        // 测试环境userID和token
        /*  微信登录
        *
            tk: "y5ExjQ9YcDwpbMUXPppkwrZId81NncgOmYbooq+PcGcjBwj1czXneg==",
            userId: "8jU598jX2YeQD4J9j32xKGjyJ8qLjzFjxiIEmpO9zObr5/SdYD5Caw=="
        * 手机号登录
               tk: "DRT120YFMWrw8joyoG0I5+7JkcBqWuhseYe9xz67kljY/NyJ9mOsVg==",
                userId: "4XwZ6FPUvv9B9FTXvcVT+LmGV+PbnGC7mSeIJBqDH4XYa5kDxVgCdw=="
        * */
        // localDB.DBSetter('userInfo', {
        //     tk: "y5ExjQ9YcDwpbMUXPppkwrZId81NncgOmYbooq+PcGcjBwj1czXneg==",
        //     userId: "8jU598jX2YeQD4J9j32xKGjyJ8qLjzFjxiIEmpO9zObr5/SdYD5Caw=="
        // })
        const noNeedAuths = ['ads_info_detail', "notice_info_detail"]
        if (!noNeedAuths.includes(pathName) && !fromWebview) {
            wxOAuth2()
        }
    }

    componentWillUnmount(): void {

    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }

    componentWillReceiveProps(nextProps: Readonly<P>, nextContext: any): void {

    }
}


function mapStateToProps(state: entries) {
    const {fromWebview} = state.commonReducer
    return {fromWebview}
}

function mapDispatchToProps(dispatch: any) {
    const {wxOAuth2} = commonActions
    return {
        ...bindActionCreators({
            wxOAuth2
        }, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App)


