import {commonActions} from 'action'
import {museumApis} from 'apis'
import {Toast} from 'antd-mobile'
import {WxConfig} from "utils";

export const Types = {
    getMuseums: 'GET_MUSEUMS',
    changeMuseumData: 'CHANGE_MUSEUM_DATA',
    getMuseumDetail: 'GET_MUSEUM_DETAIL',
    getCollections: "GET_COLLECTIONS",
    getAdsInfos: 'GET_ADS_INFO_LIST',
    getMuseumTypes: "GET_MUSEUM_TYPES",
    getMuseumSubTypes: "GET_MUSEUM_SUB_TYPES",
    getCollectionComments: 'GET_COLLECTION_COMMENTS'
}


const getMuseums = (data: any, flag: string) => async (dispatch: any) => {
    const urls: any = {
        list: {
            url: '/queryMuseumList',
            title: '线上博物馆'
        },
        search: {
            url: "/seekMuseumList",
            title: '博物馆搜索'
        },
        attention: {
            url: '/queryAttentionMuseumList',
            title: '我的关注'
        }
    }
    await WxConfig.setWxTitle(urls[flag].title)
    if (data.page === 1) {
        await dispatch({
            type: Types.changeMuseumData,
            payload: {
                museumList: []
            }
        })
    }
    const {list} = await commonActions.getPaginationData({
        url: urls[flag].url,
        data
    })(dispatch)
    await dispatch({
        type: Types.getMuseums,
        payload: {
            list
        }
    })
    return {list}
}

const changeMuseumData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeMuseumData,
        payload: data
    })
}

const getMuseumDetail = (id: string) => async (dispatch: any) => {
    const museum = await museumApis.getMuseumDetail(id)
    await dispatch({
        type: Types.getMuseumDetail,
        payload: {museum}
    })
    const {name, logoImg: {medium}, describe} = museum
    await WxConfig.setWxTitle(name)
    await WxConfig.onShare({
        title: name,
        desc: describe,
        imgUrl: medium
    })
    return museum
}

