const attentionMuseum = (data: any) => async (dispatch: any) => {
    const {isOk} = await museumApis.attentionMuseum(data)
    if (isOk) {
        Toast.show('博物馆' + (data.isAttention === 1 ? '取消' : '') + '关注成功！')
    }
    return {isOk}
}

