const getAdsInfoDetail = (id: string) => async (dispatch: any) => {
    const adsInfoDetail = await museumApis.getAdsInfoDetail({id})
    await dispatch({
        type: Types.changeMuseumData,
        payload: {adsInfoDetail}
    })
    const {title, coverImg} = adsInfoDetail
    await WxConfig.setWxTitle(title)
    await WxConfig.onShare({
        title: title,
        desc: title,
        imgUrl: coverImg.medium
    })
    return adsInfoDetail
}

const getCollectionDetail = (id: string) => async (dispatch: any) => {
    const collection = await museumApis.getCollectionDetail({id})
    await dispatch({
        type: Types.changeMuseumData,
        payload: {collection: {...collection}}
    })
    const {
        name, objectPriImg = [{medium: 'https://imgqiniu.vson.com.cn/miniprogram_assetes/logo_bg.png'}],
        describe
    } = collection
    await WxConfig.setWxTitle(name)
    await WxConfig.onShare({
        title: name,
        desc: describe,
        imgUrl: objectPriImg[0].medium
    })
    return collection
}

