import {Utils} from "utils";

const jsApiList = [
    'updateAppMessageShareData',
    'updateTimelineShareData',
    'onMenuShareAppMessage',  //旧的接口，即将废弃
    'onMenuShareTimeline', //旧的接口，即将废弃
    'onMenuShareQQ',
    "closeWindow",
    "getLocation"
]

export const config = (config: any) => {
    //@ts-ignore
    window.wx.config({
        debug: false,
        ...config,
        jsApiList
    })
    //@ts-ignore
    window.wx.error(function (res) {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，
        // 具体错误信息可以打开config的debug模式查看，
        // 也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        console.log(res)
    });
}

export const getLocation = async () => {
    return new Promise((resolve, reject) => {
        //@ts-ignore
        window.wx.getLocation({
            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res: any) {
                resolve(res)
                return res
            },
            error: function (e: any) {
                reject(e)
                return e
            }
        })
    })
}

export const onShare = (params: any) => {
    const {search, href, origin, pathname} = window.location
    const {
        title = '乐收藏',
        desc = '坚持原创、高端和精品化，致力于提供在互联网＋模式下的藏家玩乐和交易平台',
        link = origin + pathname + '#' + href.split('#')[1],
        imgUrl = 'https://apis.myvsoncloud.com/lsc_h5_hash/imgs/logo.png'
    } = params
    setWxTitle(title)

    //@ts-ignore
    window.wx.checkJsApi({
        jsApiList, // 需要检测的JS接口列表，所有JS接口列表见附录2,
        success: function (res: any) {
            console.table(res)
        }
    });
    //@ts-ignore
    window.wx.ready(() => {
            //自定义“分享给朋友”及“分享到QQ”按钮的分享内容（1.4.0）
            //@ts-ignore
            window.wx.updateAppMessageShareData({
                title, desc, link, imgUrl,
                success: function () {
                    // 设置成功
                }
            });
            //自定义“分享到朋友圈”及“分享到QQ空间”按钮的分享内容（1.4.0）
            //@ts-ignore
            window.wx.updateTimelineShareData({
                title, link, imgUrl,
                success: function () {
                    // 设置成功
                }
            });
            //获取“分享到朋友圈”按钮点击状态及自定义分享内容接口（即将废弃）
            //@ts-ignore
            window.wx.onMenuShareTimeline({
                title, link, imgUrl,
                success: function () {
                    // 用户点击了分享后执行的回调函数
                }
            })

            //    获取“分享给朋友”按钮点击状态及自定义分享内容接口（即将废弃）
            //@ts-ignore
            window.wx.onMenuShareAppMessage({
                title, desc, link, imgUrl,
                // type: '', // 分享类型,music、video或link，不填默认为link
                // dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
                success: function () {
                    // 用户点击了分享后执行的回调函数
                }
            });
            //  获取“分享到QQ”按钮点击状态及自定义分享内容接口（即将废弃）
            //@ts-ignore
            window.wx.onMenuShareQQ({
                title, desc, link, imgUrl,
                success: function () {
                    // 用户确认分享后执行的回调函数
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });

        }
    )
    //@ts-ignore
    window.wx.error(function (res) {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，
        // 具体错误信息可以打开config的debug模式查看，
        // 也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
    });
}

export const getWxCode = () => {
    /*
       href: "http://apis.myvsoncloud.com/build/#/mall/auction_goods_detail/53c398273ddf40f0ace5b8bb375dddc1"
       hash: "#/mall/auction_goods_detail/53c398273ddf40f0ace5b8bb375dddc1"
       origin: "http://apis.myvsoncloud.com"
       pathname: "/build/"
       host、hostname: "apis.myvsoncloud.com"
   * */
    const {search, href, origin, pathname} = window.location
    const oauth2Host = 'https://open.weixin.qq.com/connect/oauth2/authorize?'
    const obj = {
        appid: 'wx22dc4d474b52a4b2',
        redirect_uri: origin + pathname + '#' + href.split('#')[1],
        response_type: 'code',
        scope: 'snsapi_userinfo', // snsapi_base  snsapi_userinfo
        state: 'authorized',
        connect_redirect: '1#wechat_redirect'
    }
    const getCodeUrl = oauth2Host + Utils.spliceParams(obj)
    window.location.replace(getCodeUrl)
    // window.location.href = oauth2Host + Utils.spliceParams(obj)
    return getCodeUrl
}

/**
 * 微信浏览器中设置对应页面的标题
 * 解决：IOS微信浏览器中用document.title 设置标题无效
 * */
export const setWxTitle = (title: string) => {
    document.title = title;
    const iframe: any = document.createElement("iframe");
    iframe.setAttribute("src", "https://apis.myvsoncloud.com/lsc_h5_hash/imgs/logo.png");
    iframe.setAttribute("style", "display:none");
    iframe.addEventListener('load', function () {
        setTimeout(function () {
            try {
                iframe.removeEventListener('load');
            } catch (err) {
            }
            document.body.removeChild(iframe);
        }, 0);
    });
    document.body.appendChild(iframe);
};
