import {ActionType} from 'action'
import {Types} from 'action/xiaomei'

export const initState: XiaomeiEntry = {
    noticeInfoDetail: {}
};

export interface XiaomeiEntry {
    noticeInfoDetail: any
}

export function xiaomeiReducer(
    state = initState,
    action: ActionType
) {
    const {
        type, payload = {}
    } = action
    const {} = state
    const {} = payload
    switch (type) {
        case Types.changeXiaomeiData:
            Object.assign(state, payload)
            break;
        default:
            break;
    }
    return {...state}
}

