import {Modal} from 'antd-mobile'

const {alert: confirm} = Modal

export const Confirm = (options: any) => {
    const {
        title = '提示',
        msg = '内容',
        onConfirm = () => {
        },
        onCancel = () => {
        },
        confirmText = '确定',
        cancelText = '取消'
    } = options
    confirm(title, msg, [
        {text: cancelText, onPress: onCancel},
        {text: confirmText, onPress: onConfirm}
    ])
}

export const showDownloadModal = () => confirm('提示', '下载APP了解更多精彩内容，是否前去下载？', [
    {text: '取消', onPress: () => console.log('cancel')},
    {
        text: '确定', onPress: () => {
            window.location.href = 'http://www.leshoucang.com.cn/wuweizhai_APP.htm'
        }
    },
])
