import React, {ReactNode} from 'react'
import {Modal} from 'antd-mobile'
import './index.scss'
import {entries} from "reducer";
import {commonActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";

interface P {
    className?: string,
    history: any,
    match: any,
    toggleModal: any,
    showModal: boolean,
    children: ReactNode,
    props?: any
}

interface S {
}

class VsonModal extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '', children,
                showModal,
                props = {}
            },
            onClose,
            state: {}
        } = this
        return (
            <Modal className={`vson_modal ${className}`} closable={true} transparent={true} visible={showModal}
                   onClose={onClose}
                   title={' '} {...props}>
                {children}
            </Modal>
        );
    }

    onClose = async () => {
        const {toggleModal} = this.props
        await toggleModal()
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {showModal} = state.commonReducer
    return {showModal}
}

function mapDispatchToProps(dispatch: any) {
    const {toggleModal} = commonActions
    return {
        ...bindActionCreators({toggleModal}, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VsonModal)
