import React from 'react'
import {Flex} from 'antd-mobile'
import './index.scss'
import {EmptyData} from 'components'
import {entries} from "reducer";
import {connect} from "react-redux";

interface P {
    className?: string,
    loadMore: boolean,
    list: any[]
}

interface S {
}

class CollectionComments extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                list = [],
                loadMore
            },
            state: {}
        } = this
        return (
            <div className={`collection_comments_list`}>
                {
                    list.map((e: any, i: number) => <Flex key={i} className={`comment ${i > 0 ? 'bd_u' : ''}`}>
                        <img src={e.user.headImg.medium} alt="" />
                        <Flex.Item>
                            <div className={'name'}>{e.user.nickName}</div>
                            <div className={'content'}>{e.content}</div>
                            <div className={'time'}>{e.showTime.zh}</div>
                        </Flex.Item>
                    </Flex>)
                }
                {/*{*/}
                {/*    !loadMore && !list.length && <EmptyData />*/}
                {/*}*/}
            </div>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {loadMore} = state.commonReducer
    return {
        loadMore
    }
}

function mapDispatchToProps(dispatch: any) {
    return {};
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CollectionComments)
