import {ActionType} from 'action'
import {Types} from 'action/lxt'

export const initState: LxtEntry = {
    noticeInfoDetail: {}
};

export interface LxtEntry {
    noticeInfoDetail: any
}

export function lxtReducer(
    state = initState,
    action: ActionType
) {
    const {
        type, payload = {}
    } = action
    const {} = state
    const {} = payload
    switch (type) {
        case Types.changeLxtData:
            Object.assign(state, payload)
            break;
        default:
            break;
    }
    return {...state}
}

