import React from 'react'
import {EmptyData} from 'components'
import {Flex} from 'antd-mobile'
import {Link} from 'react-router-dom'
import './index.scss'

interface P {
    list: any[]
}

interface S {
}

class StoreList extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {list = []},
            state: {}
        } = this
        return (
            <div className={`store_list padding_lr`}>
                {
                    list.map((e: any, i: number) => <Flex className={'store bd_d padding_ud'} key={i}>
                        <img className={'logo'} src={e.storeImg.medium} alt="" />
                        <Flex.Item>
                            <div className={'name'}>{e.storeName}</div>
                            <div className={'score'}>店铺评分：{e.storeGrade}</div>
                        </Flex.Item>
                        <Link to={{pathname: '/mall/store_home/' + e.storeId}}>进店</Link>
                    </Flex>)
                }
                {
                    !list.length && <EmptyData />
                }
            </div>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default StoreList
