import React from 'react'
import {Flex} from 'antd-mobile'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {FixedLinesText} from 'components'
import {entries} from "reducer";
import {treasureForumActions} from "action";
import {Dialog} from 'utils'
import './index.scss'

interface P {
    treasure: any,
    praiseTreasure: any,
    attentionTreasure: any,
    collectTreasure: any,
    onOperate?: any
}

interface S {
}


class TreasureItem extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                treasure: {author, share, showTime, imgs = [], ...others},
            },
            state: {},
            onOprs
        } = this
        return (
            <Flex className={'treasure_item padding_ud bd_d'} onClick={Dialog.showDownloadModal}>
                <img className={'header'} src={author.headImg.medium} alt="" />
                <Flex.Item>
                    <div className={'user_name'}>{author.nickName}</div>
                    <div className={'date_time padding_ud'}>
                        <span>藏友交流</span>
                        {showTime.zh}
                    </div>
                    <FixedLinesText className={'content'} text={others.content} />
                    <Flex className={`imgs ${imgs.length === 1 ? 'single' : 'multi'}`}>
                        {
                            imgs.map((e: any, i: number) => <img key={i} src={e.medium} alt={''} />)
                        }
                    </Flex>
                    <Flex className={`oprs padding`}>
                        <div onClick={(e) => onOprs(e, 1)}>
                            <img src={'/lsc_h5_hash/imgs/like_' + others.isPraise + '.png'} alt="" />
                            <span className={`${others.isPraise === '0' ? 'active' : ''}`}>{others.praiseNum}</span>
                        </div>
                        <div>
                            <img src={'/lsc_h5_hash/imgs/comment.png'} alt="" />
                            <span>{others.reviewNum}</span>
                        </div>
                        <div onClick={(e) => onOprs(e, 2)}>
                            <img src={'/lsc_h5_hash/imgs/collect_' + others.isFavorite + '.png'} alt="" />
                            <span
                                className={`${others.isFavorite === '0' ? 'active' : ''}`}>{others.isFavorite === '1' ? '收藏' : '已收藏'}</span>
                        </div>
                        <div onClick={(e) => onOprs(e, 3)}>
                            <img src={'/lsc_h5_hash/imgs/attention_' + others.isAttention + '.png'} alt="" />
                            <span
                                className={`${others.isAttention === '0' ? 'active' : ''}`}>{others.isAttention === '1' ? '关注' : '已关注'}</span>
                        </div>
                    </Flex>
                </Flex.Item>
            </Flex>
        );
    }

    componentDidMount(): void {

    }

    onOprs = async (e: any, tag: number) => {
        e.stopPropagation()
        const {
            treasure,
            praiseTreasure,
            attentionTreasure,
            collectTreasure,
            onOperate = () => {
            }
        } = this.props
        let res
        switch (tag) {
            case 1:
                res = await praiseTreasure(treasure.sayId)
                break;
            case 2:
                res = await collectTreasure(treasure.sayId, treasure.isFavorite === '0' ? 0 : 1)
                break;
            case 3:
                res = await attentionTreasure([treasure.author.userId], treasure.isAttention === '1' ? 0 : 1)
                break;
            default:
                break
        }
        const {isOk} = res
        if (isOk) await onOperate()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

const mapStateToProps = (state: entries) => {
    return {}
}

const mapDispatchToProps = (dispatch: any) => {
    const {praiseTreasure, attentionTreasure, collectTreasure} = treasureForumActions
    return {
        ...bindActionCreators({
            praiseTreasure, attentionTreasure, collectTreasure
        }, dispatch),
        dispatch
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(TreasureItem)
