import {ActionType} from 'action'
import {Types} from 'action/treasure_forum'

export const initState: TreasureForumEntry = {
    treasureForumList: [],
    treasureTypes: [],
    treasure: {}
};

export interface TreasureForumEntry {
    treasureForumList: any[],
    treasureTypes: any[],
    treasure: any
}

export function treasureForumReducer(
    state = initState,
    action: ActionType
) {
    const {
        type, payload = {}
    } = action
    const {
        treasureForumList
    } = state
    const {
        list = []
    } = payload
    switch (type) {
        case Types.getTreasure:
            state.treasureForumList = [...treasureForumList, ...list]
            break;
        case Types.changeTreasureData:
            Object.assign(state, {...payload})
            break;
        case Types.getTreasureTypes:
            state.treasureTypes = [...list]
            break;
        default:
            break;
    }
    return {...state}
}

