import React from 'react'
import {ImagePicker, Toast} from 'antd-mobile'
import {localDB} from 'utils'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {commonActions} from 'action'
import {entries} from 'reducer'
import {withRouter} from 'react-router-dom'

import './index.scss'

interface P {
    name: string,
    className?: string,
    files?: any[],
    getUploadToken: any,
    uploadQiniu: any,
    qiniuToken: any,
    wxOAuth2: any,
    userInfo: any,
    formFields: any,
    changeCommonData: any,
    history: any,
    limit?: number,
    lineImg?: number,
    mod?: string,
    type?: any
}

interface S {
}

const prefixes: any = {
    museum: 'img/lsc/museum'
}
const imgHost = 'https://imgqiniu.vson.com.cn/'


class VsonUpload extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                name, formFields,
                lineImg = 3, limit = 1,
                type = 'img'
            },
            state: {},
            onChange,
            onImageClick
        } = this
        const fileList = formFields[name] || []
        let list: any[] = []
        fileList.map((e: any, i: number) => {
            list.push(!e.url ? {
                ...e,
                url: e.imgUrl || e.coverImg
            } : e)
        })
        return (
            <ImagePicker
                className={`vson_upload ${className}`}
                multiple={limit - list.length > 1}
                files={list}
                length={lineImg}
                selectable={list.length < limit}
                onChange={onChange}
                accept={type === 'video' ? 'video/*' : 'image/*'}
                onImageClick={onImageClick}
            />
        );
    }

    onImageClick = async (index?: number, files?: object) => {

    }

    onChange = async (files: Array<{}>, operationType: string, index?: number) => {
        // operationType add    remove
        const {name, formFields, changeCommonData, limit = 1} = this.props
        if (operationType === 'remove') {
            formFields[name] = files
            await changeCommonData({formFields: {...formFields}})
            return
        }
        if (operationType === 'add') {
            files.map(async (e: any, index: number) => {
                if (!!e.file && index < limit) {
                    await this.onUpload(e.file)
                }
            })
            return
        }

    }

    onUpload = async (file: any) => {
        let {
            getUploadToken, wxOAuth2, qiniuToken, userInfo, uploadQiniu, formFields, changeCommonData, name, mod = 'museum', type = 'img'
        } = this.props
        userInfo = JSON.parse(localDB.DBGetter('userInfo') || '{}')
        if (!userInfo || !userInfo.userId) {
            await wxOAuth2()
            return
        }
        if (!qiniuToken) {
            qiniuToken = await getUploadToken()
        }
        const {type: fileType} = file

        const formData = new FormData()
        formData.append('file', file)
        formData.append('key', prefixes[mod] + '/' + userInfo.userId + '/' + new Date().getTime() + '.' + fileType.split('/')[1])
        formData.append('token', qiniuToken.token)
        Toast.loading((type === 'video' ? '3D动画或视频' : '图片') + '上传中...', 10)
        const {fsize, hash, key} = await uploadQiniu(formData)
        Toast.hide()
        const arr = formFields[name] || []
        if (type === 'video') {
            arr.push({
                coverImg: 'https://imgqiniu.vson.com.cn/miniprogram_assetes/logo_bg.png',
                shopVideo: imgHost + key
            })
            formFields[name] = arr
            await changeCommonData({formFields: {...formFields}})
        } else {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = async (evt: any) => {
                const image: any = new Image();
                image.src = evt.target.result;
                image.onload = async () => {
                    const {width, height} = image
                    arr.push({
                        imgUrl: imgHost + key,
                        imgCategory: 1,
                        width,
                        height
                    })
                    formFields[name] = arr
                    await changeCommonData({formFields: {...formFields}})
                }
            }
        }
        return {
            imgUrl: imgHost + key,
            imgCategory: 1,
            fsize, hash, key
        }
    }

    getFiles = async (props?: any) => {
        const {name, formFields, changeCommonData, type = 'img'} = props || this.props
        if (!props) {
            formFields[name] = []
        } else {
            const files = formFields[name] || []
            let arr: any[] = []
            // @ts-ignore
            files.map((e: any) => {
                const {imgUrl, original, coverImg, video} = e
                arr.push(type === 'video' ?
                    {coverImg: coverImg.original, shopVideo: video}
                    : {
                        imgUrl: imgUrl || original,
                        width: 200,
                        height: 100,
                        imgCategory: 1
                    })
            })
            formFields[name] = arr
        }
        await changeCommonData({formFields: {...formFields}})
    }

    componentDidMount(): void {
        this.getFiles()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }

    componentWillReceiveProps(nextProps: Readonly<P>, nextContext: any): void {
        const {name, formFields: {[name]: list = []}} = this.props
        const {name: nName, formFields: {[nName]: nList = []}} = nextProps
        if (name !== nName || list.length !== nList.length) {
            this.getFiles(nextProps)
        }
    }
}

function mapStateToProps(state: entries) {
    const {qiniuToken, userInfo, formFields} = state.commonReducer
    return {
        qiniuToken, userInfo, formFields
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getUploadToken, uploadQiniu, changeCommonData, wxOAuth2} = commonActions
    return {
        ...bindActionCreators({getUploadToken, uploadQiniu, wxOAuth2, changeCommonData}, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(VsonUpload))
