import {xiaomeiApis} from 'apis'

export const Types = {
    changeXiaomeiData: 'CHANGE_XIAOMEI_DATA',
}

const changeXiaomeiData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeXiaomeiData,
        payload: data
    })
}
const getNoticeInfoDetail = (id: string) => async (dispatch: any) => {
    const noticeInfoDetail = await xiaomeiApis.getNoticeInfoDetail({id})
    await dispatch({
        type: Types.changeXiaomeiData,
        payload: {noticeInfoDetail}
    })
    return noticeInfoDetail
}


export {
    changeXiaomeiData,
    getNoticeInfoDetail
}

