import Tloader from 'react-touch-loader'
import React, {ReactNode} from 'react'
import './index.scss'
import {entries} from "reducer";
import {commonActions, mallActions, treasureForumActions, museumActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";


export interface ComponentsProps {
    dispatch: any,
    refreshOpts?: any,
    onScroll?: any
    onLoadData?: any,
    className?: string,
    children: ReactNode,
    page: number,
    totalPage: number,
    changeCommonData: any,
    changeMallData: any,
    changeTreasureData: any,
    changeMuseumData: any,
    loadMore?: boolean
}

const screenH = window.screen.height

let scrollNode: any = null

class ScrollVertical extends React.Component<ComponentsProps> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            handleRefresh,
            handleLoadMore,
            props: {
                children,
                page,
                totalPage,
                className
            }
        } = this
        return (
            <Tloader
                className={`vson_scroll ${className}`}
                onRefresh={handleRefresh}
                onLoadMore={handleLoadMore}
                initializing={0}
                hasMore={page < totalPage}
                autoLoadMore={true}
            >
                {
                    children
                }
            </Tloader>
        );
    }

    handleScroll = (event: any) => {
        // event.persist()
        const {
            onScroll = () => {
            }
        } = this.props
        onScroll(scrollNode)
        this.onLazyLoad()
    }

    onLazyLoad = () => {
        const arr = [].slice.call(document.getElementsByClassName('lazy_img'));
        arr.map((e: any) => {
            const {bottom, top} = e.getBoundingClientRect()
            if (bottom < 0 || top > screenH) return
            const src = e.getAttribute('data-src')
            if (!!src) {
                e.setAttribute('src', src)
                e.setAttribute('data-src', '')
            }
        })
    }

    handleRefresh = async (resolve: any, reject: any) => {
        const {
            onLoadData = () => {
            },
            refreshOpts = [],
            dispatch
        } = this.props
        await this.initData()
        for (let i = 0; i < refreshOpts.length; i++) {
            await dispatch(refreshOpts[i])
        }
        await onLoadData()
        resolve()
    }

    handleLoadMore = async (resolve: any) => {
        const {
            onLoadData = () => {
            },
            page,
            totalPage,
            loadMore,
            changeCommonData
        } = this.props
        if (!loadMore) {
            await changeCommonData({page: page + 1, loadMore: true})
            await onLoadData()
        }
        resolve()
    }

    componentDidMount(): void {
        const {handleScroll, initData} = this
        scrollNode = document.getElementsByClassName('tloader')[0]
        scrollNode.addEventListener('scroll', handleScroll)
        initData()
    }

    initData = async () => {
        const {props: {changeMallData, changeCommonData, changeTreasureData, changeMuseumData}} = this
        await changeCommonData({page: 1, totalPage: 0, storeInfo: {}})
        await changeMallData({goodsList: [], storeList: []})
        await changeTreasureData({treasureForumList: []})
        await changeMuseumData({
            museumList: [],
            collectionComments: [],
            collectionList: [],
            museumSubTypes: [],
            museumTypes: []
        })
    }

    componentDidUpdate(prevProps: Readonly<ComponentsProps>, prevState: Readonly<{}>, snapshot?: any): void {
        this.onLazyLoad()
    }

    componentWillUnmount(): void {
        const {handleScroll} = this
        scrollNode.removeEventListener('scroll', handleScroll)
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {page, totalPage, loadMore} = state.commonReducer
    return {
        page,
        totalPage,
        loadMore
    }
}

function mapDispatchToProps(dispatch: any) {
    const {changeCommonData} = commonActions
    const {changeMallData} = mallActions
    const {changeTreasureData} = treasureForumActions
    const {changeMuseumData} = museumActions
    return {
        ...bindActionCreators({changeCommonData, changeMallData, changeTreasureData, changeMuseumData}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ScrollVertical)

