import {Toast} from 'antd-mobile';
import {localDB, Utils, Dialog, WxConfig} from 'utils'
import {commonActions} from 'action'

/*
* 一个配置项对象，包括所有对请求的设置。可选的参数有：
method: 请求使用的方法，如 GET、POST。
headers: 请求的头信息，形式为 Headers 的对象或包含 ByteString 值的对象字面量。
body: 请求的 body 信息：可能是一个 Blob、BufferSource、FormData、URLSearchParams 或者 USVString 对象。注意 GET 或 HEAD 方法的请求不能包含 body 信息。
mode: 请求的模式，如 cors、 no-cors 或者 same-origin。
credentials: 请求的 credentials，如 omit、same-origin 或者 include。为了在当前域名内自动发送 cookie ， 必须提供这个选项， 从 Chrome 50 开始， 这个属性也可以接受 FederatedCredential 实例或是一个 PasswordCredential 实例。
cache:  请求的 cache 模式: default 、 no-store 、 reload 、 no-cache 、 force-cache 或者 only-if-cached 。
redirect: 可用的 redirect 模式: follow (自动重定向), error (如果产生重定向将自动终止并且抛出一个错误), 或者 manual (手动处理重定向). 在Chrome中，Chrome 47之前的默认值是 follow，从 Chrome 47开始是 manual。
referrer: 一个 USVString 可以是 no-referrer、client或一个 URL。默认是 client。
referrerPolicy: Specifies the value of the referer HTTP header. May be one of no-referrer、 no-referrer-when-downgrade、 origin、 origin-when-cross-origin、 unsafe-url 。
integrity: 包括请求的  subresource integrity 值 （ 例如： sha256-BpfBw7ivV8q2jLiT13fxDYAe2tJllusRSZ273h2nFSE=）。
*
  "proxy": {
      "/api/*":  {
        "target": "https://ts.vson.com.cn:26082",
        "secure": false
      }
    }
*
* */

export interface FetchOptions {
    method?: string,
    headers?: HeadersInit,
    body?: any,
    mode?: any,
    credentials?: any,
    cache?: any,
    env?: string,
    domain?: string
}

const Hosts: any = {
    dev: 'https://ts.vson.com.cn:26082',
    pro: 'https://apis.myvsoncloud.com'
}
const domains: any = {
    m: '/vc-lscmas-ws-1.0.2/api/m',
    all: '/vc-lsc-ws-1.0.2/api/all',
    com: '/vc-css-ws-1.0.2/api/all',
    pm: '/vc-pms-ws-1.0.2/api/all',
    air: '/vc-air-ws-1.0.2/api/air',
    lxt: '/vc-ccs-ws-1.0.2/api/all'
}

const commonHeaders: any = {
    ost: 2,
    apv: '1.0',
    apt: 107
}

const contentTypes = [
    "application/x-www-form-urlencoded",
    'application/json;charset=UTF-8',
    "multipart/form-data"
]

export const Fetch = async (url: string, data: any = {}, options: any = {}) => {
    const {
        method = 'POST',
        headers = {},
        contentType = 0,
        mode = "cors",
        credentials = 'include',
        cache = "default",
        env = 'dev',
        domain = 'all',
        needLogin = false,
        doStatic = false
    } = options
    const {tk} = JSON.parse(localDB.DBGetter('userInfo') || "{}")
    if (!!tk) {
        commonHeaders.tk = tk
    } else if (needLogin) {
        if (!doStatic) {
            Dialog.Confirm({
                msg: '您尚未登录，马上去授权登录？',
                onConfirm: commonActions.wxOAuth2
            })
        }
        return {}
    }
    if (url.indexOf('http') !== 0) {
        // url = Hosts[env] + domains[domain] + url
        // url = '/lsc_h5_hash/lsc_h5_api' + domains[domain] + url
        // url = '/lsc_h5' + domains[domain] + url
        url = domains[domain] + url
    }
    const params = Utils.getFetchParams(data || {}, contentType)
    let result: any = {}
    try {
        await fetch(url, {
            method,
            headers: {
                ...commonHeaders,
                "content-type": contentTypes[contentType],
                ...headers
            },
            body: params,
            mode,
            credentials,
            cache
        })
            .then(async res => {
                const {ok, statusText} = res
                if (ok) {
                    return await res.json()
                } else {
                    Toast.fail(statusText)
                    return {}
                }
            }, async err => {
                Toast.fail(err)
            })
            .then((response: any) => {
                const {retCode, result: res = {isOk: true}} = response
                if (retCode === '0') {
                    result = {...res, isOk: true}
                    return result
                } else if (retCode === '1003') {
                    Dialog.Confirm({
                        msg: "登录信息已过期，重新登录？",
                        onConfirm: WxConfig.getWxCode,
                        onCancel: () => {
                            localDB.DBRemove('userInfo')
                        }
                    })
                    return {isOk: false}
                } else {
                    Toast.fail(JSON.stringify(res))
                    return {isOk: false}
                }
            }, async err => {
                Toast.fail(err)
            })
            .catch(error => {
                throw (error)
            })
    } catch (e) {
        Toast.fail('请求错误!');
        throw (e)
    }

    return result
}

