import {Utils, Fetch, decryptByDes} from "utils";

export const getGoodsCategories = async () => {
    const {shopTypeList = []} = await Fetch('/queryShopTypeList')
    return {goodsCategories: shopTypeList}
}

export const getPaginationData = async (obj: any) => {
    const {url, data} = obj
    const {totalPage, ...others} = await Fetch(url, {...data});
    let res: any = {
        totalPage: parseInt(totalPage),
        data: {}
    }
    Object.entries(others).map(([key, val]) => {
        if (val && Utils.getDataType(val) === 'Array') {
            res[!res.list ? 'list' : key] = val
        } else {
            res.data[key] = val
        }
    })
    return res
}

export const login = async (data: object) => {
    return await Fetch('/login', data, {domain: 'm'})
}

export const getWxConfig = async (data: object) => {
    return await Fetch('/weixinWebpageLogin', data, {domain: 'm'})
}

export const getUserStore = async () => {
    return await Fetch('/setStore', {}, {needLogin: true})
}

export const getUploadToken = async () => {
    const {expireSeconds, token} = await Fetch('/qiniuUploadImgToken', {}, {domain: 'com'})
    return {
        token: decryptByDes(token, 'header'),
        expireSeconds
    }
}

export const uploadQiniu = async (data: any) => {
    return await fetch('https://up.qiniup.com', {
        // headers: {"Content-Type": "multipart/form-data"},
        body: data,
        method: 'post'
    }).then(res => res.json()).then(res => res)
}

export const getCities = async (type: string) => {
    return await Fetch('/queryCityList', {type})
}

export const getSubCities = async (number: string) => {
    return await Fetch('/queryCityTypeList', {number})
}

export const getMyMuseum = async () => {
    return await Fetch('/myMuseum', {})
}
