import React from 'react'
import './index.scss'

interface P {
    lines?: number,
    className?: string,
    text: string
}

interface S {
    showAll: boolean
}


class FixedLinesText extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {
            showAll: false
        };
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                lines = 5,
                className = '',
                text = ''
            },
            state: {showAll},
            toggleShowAll
        } = this
        return (
            <div className={`fixed_lines_text ${className}`}>
                {text.length > 100 ? <div>
                    <p className={showAll ? 'show_all' : ''} style={{maxHeight: lines * 1.5 + 'rem'}}>{text}</p>
                    <span onClick={toggleShowAll}>{showAll ? '收起全文' : '展开全文'}</span>
                </div> : text}
            </div>
        );
    }

    toggleShowAll = (e: any) => {
        e.stopPropagation()
        const {showAll} = this.state
        this.setState({showAll: !showAll})
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default FixedLinesText
