import React from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {entries} from 'reducer'
import {museumActions, commonActions} from 'action'
import {Toast} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import './index.scss'

interface P {
    className?: string,
    collection: any,
    index: number,
    type?: string,
    edit?: boolean,
    fromWebview?: boolean,
    history: any,
    favoriteCollection: any,
    changeCommonData: any,
    changeMuseumData: any,
    getCollections: any,
    collectionList: any[]
}

interface S {
}

class CollectionItem extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                collection,
                type,
                edit = false
            },
            state: {},
            chose,
            navTo,
            onFavorite
        } = this
        return (
            <div className={`collection_item clearfix`} onClick={navTo}>
                {
                    edit && <img className={'checkbox_img'} onClick={chose}
                                 src={collection.checked ? '/lsc_h5_hash/imgs/chose1.png' : '/lsc_h5_hash/imgs/chose2.png'}
                                 alt="" />
                }
                <img className={'logo'} src={collection.objectPriImg[0].medium} alt="" />
                <div className={'name'}>{collection.name}</div>
                <div className="describe">{collection.describe}</div>
                {
                    type === 'favorite' &&
                    <img onClick={onFavorite} className={'del'} src="/lsc_h5_hash/imgs/delete.png" alt="" />
                }
            </div>
        );
    }

    navTo = async (e: any) => {
        e.stopPropagation()
        const {collection: {objectId}, edit, history, fromWebview} = this.props
        history.push({pathname: edit ? '/museums/collection_edit/' + objectId + '/edit' : "/museums/collection_detail/" + objectId + '/' + fromWebview})
    }

    chose = async (e: any) => {
        e.stopPropagation()
        const {collectionList, index, changeMuseumData} = this.props
        collectionList[index].checked = !collectionList[index].checked
        await changeMuseumData({collectionList: [...collectionList]})
    }

    onFavorite = async (e: any) => {
        e.stopPropagation()
        const {favoriteCollection, changeCommonData, getCollections, collection: {objectId}} = this.props
        const {isOk} = await favoriteCollection({id: objectId, status: 0})
        if (isOk) {
            Toast.success('取消收藏成功！')
            await changeCommonData({page: 1})
            await getCollections({
                url: '/queryFavoriteMuseumObject',
                data: {page: 1}
            })
        }
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {collectionList} = state.museumReducer
    const {fromWebview} = state.commonReducer
    return {collectionList, fromWebview}
}

function mapDispatchToProps(dispatch: any) {
    const {favoriteCollection, getCollections, changeMuseumData} = museumActions
    const {changeCommonData} = commonActions
    return {
        ...bindActionCreators({favoriteCollection, changeCommonData, changeMuseumData, getCollections}, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(CollectionItem))
