import React from 'react'
import {Flex} from 'antd-mobile'
import {Link} from 'react-router-dom'
import {AllowRight} from 'components'

import './index.scss'

export interface P {
    theme: string,
    params?: any[]
}

const themes: any = {
    mall: {
        page: '',
        title: '店铺评价'
    },
    museum: {
        page: '/museums/collection_comments',
        title: '评价'
    }
}

class CommentsNav extends React.Component<P> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {theme,params=[]}
        } = this
        return (
            <AllowRight className={'item padding_lr comments_nav'}>
                <Flex.Item className={'title ' + theme}>{themes[theme].title}</Flex.Item>
                <Link to={themes[theme].page+'/'+params.join('/')} className={'right_tip'}>查看全部评价</Link>
            </AllowRight>
        );
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default CommentsNav
