import React from 'react'
import {commonActions, mallActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {entries} from "reducer";
import {VsonTabs, GoodsList, WingLine} from 'components'

import './index.scss'

export interface P {
    changeCommonData: any,
    changeMallData: any,
    getStoreHome: any,
    page: number,
    loadMore: boolean,
    id: string,
    goodsList: any[],
    formFields: any,
    goodsTypes: any[],
    sortRules: any[],
    className?: string
}

class StoreGoods extends React.Component<P> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {goodsList, goodsTypes, sortRules, className = ''},
            onTabChange
        } = this
        return (
            <div className={`${className} store_goods`}>
                <WingLine>
                    店铺商品
                </WingLine>
                <VsonTabs name={'shopType'} options={goodsTypes} onChange={onTabChange} />
                <VsonTabs name={'sort'} options={sortRules} onChange={onTabChange} />
                <GoodsList list={goodsList} />
            </div>
        );
    }

    componentDidMount(): void {
        this.getInitData()
    }

    getInitData = async (props?: any) => {
        const {getStoreHome, id, page, formFields: {sort = 0, shopType = 1}, changeCommonData} = props || this.props
        if (!id) return
        await getStoreHome({storeId: id, shopType, page, sort})
        await changeCommonData({loadMore: false})
    }

    onTabChange = async () => {
        const {
            props: {changeCommonData, changeMallData},
            getInitData
        } = this
        await changeMallData({goodsList: []})
        await changeCommonData({
            page: 1,
            totalPage: 0
        })
        await getInitData()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }

    componentWillReceiveProps(nextProps: Readonly<P>, nextContext: any): void {
        const {page: newPage, id: newId, loadMore: nLoad} = nextProps
        const {page, id, loadMore} = this.props
        // if (newId && (newId !== id || page !== newPage || shopType !== newType)) {
        if (newId !== id || nLoad && !loadMore || page !== newPage) {
            this.getInitData(nextProps)
        }
    }
}

function mapStateToProps(state: entries) {
    const {goodsList} = state.mallReducer
    const {formFields, goodsTypes, page, sortRules, loadMore} = state.commonReducer
    return {
        page,
        loadMore,
        goodsList,
        formFields,
        sortRules,
        goodsTypes: [...goodsTypes].slice(0, 3)
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getStoreHome, changeMallData} = mallActions
    const {changeCommonData} = commonActions
    return {
        ...bindActionCreators({
            changeMallData,
            changeCommonData,
            getStoreHome
        }, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(StoreGoods)
