import React from 'react'
import {withRouter} from 'react-router-dom'
import './index.scss'

interface P {
    history: any,
    className?: string,
    hasBottom?: boolean,
    data?: any
}

interface S {
}


class BackHome extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {className = '', hasBottom = false},
            state: {},
            goHome
        } = this
        return (
            <div className={`home_img ${className} ${hasBottom ? 'height' : ''}`} onClick={goHome}>
                <img src={'/lsc_h5_hash/imgs/home1.png'} alt='' />
            </div>
        );
    }

    componentDidMount(): void {
    }

    goHome = () => {
        const {history, data = {}} = this.props
        const {museumId} = data
        const [a, mod, pathname, ...c] = window.location.hash.split('/')
        switch (true) {
            case pathname === 'collection_detail':
                history.push({pathname: '/museums/museum_home/' + museumId})
                break;
            case mod === 'museums':
                history.push({pathname: '/home/museum'})
                break
            default:
                history.push({pathname: '/home/mall_home'})
                break;
        }
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default withRouter(BackHome)
