import React from 'react'
import {Flex} from 'antd-mobile'
import {entries} from "reducer";
import {museumActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {withRouter} from 'react-router-dom'
import './index.scss'


interface P {
    className?: string,
    showStyle?: string,
    museum: any,
    attentionMuseum: any,
    history: any,
    myMuseums: any[]
}

interface S {
}


class MuseumItem extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                showStyle = 'grid',
                museum
            },
            state: {},
            onAttention,
            navTo
        } = this
        return showStyle === 'grid' ? <div className={`museum_item_grid `} onClick={navTo}>
                <img className={'logo'} src={museum.imgs[0].medium} alt="" />
                <div className={'info clearfix'}>
                    <Flex className={'name'}>
                        <Flex.Item>{museum.name}</Flex.Item>
                        <span>{museum.nature == 1 ? '私立' : '公立'}</span>
                    </Flex>
                    <div className={'describe'}>{museum.describe}</div>
                    <img onClick={onAttention} className={'favorite'}
                         src={'/lsc_h5_hash/imgs/' + (museum.isAttention == 1 ? 'collection.png' : 'collection_active.png')} alt="" />
                </div>
            </div> :
            <Flex className={`museum_item_distance bd_d`} onClick={navTo}>
                <img className={'logo'} src={museum.imgs[0].medium} alt="" />
                <Flex.Item>
                    <div className={'name clearfix'}>
                        {museum.name}
                        <span>{museum.nature == 1 ? '私立' : '公立'}</span>
                    </div>
                    <Flex className={`describe`}>
                        <Flex.Item className={'single_line'}>
                            {museum.describe}
                        </Flex.Item>
                        <div className={'state2 goto'}>去这里</div>
                    </Flex>
                    <div className={'distance'}>距离:<span> {parseInt(museum.distance * 10 + '') / 10}km</span></div>
                </Flex.Item>
            </Flex>
    }

    navTo = (e: any) => {
        const {museum: {museumId}, history, myMuseums} = this.props
        const index = myMuseums.findIndex((e: any) => e.museumId === museumId)
        history.push({pathname: '/museums/collection_list/museum/' + museumId + '/' + (index !== -1)})
    }
    onAttention = async (e: any) => {
        e.stopPropagation()
        const {museum: {museumId, isAttention}, attentionMuseum} = this.props
        await attentionMuseum({id: museumId, isAttention: isAttention == 1 ? 0 : 1})
    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {myMuseums} = state.commonReducer
    return {myMuseums}
}

function mapDispatchToProps(dispatch: any) {
    const {attentionMuseum} = museumActions
    return {
        ...bindActionCreators({attentionMuseum}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(withRouter(MuseumItem))
