import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'

import './index.scss'

export interface P {
    className?: string,
    placeholder?: string,
    page?: string,
    navData?: any,
    history: any
}

class SearchNav extends Component<P> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {props: {placeholder = '请输入搜索内容',className=''}, navTo} = this;
        return (
            <div className={`search_nav ${className}`} onClick={navTo}>
                <img src="/lsc_h5_hash/imgs/search1.png" alt="" />
                <span>{placeholder}</span>
            </div>
        );
    }

    navTo = () => {
        const {navData = [], history, page = '/mall/search_goods/'} = this.props
        history.push(page + navData.join('/'))
    }


    componentDidMount(): void {
    }

    componentWillMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default withRouter(SearchNav)
