import React from 'react'
import {CollectionItem, EmptyData} from 'components'
import './index.scss'

interface P {
    className?: string,
    list: any[],
    type?: string,
    edit?:boolean
}

interface S {
}


class CollectionList extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                list,
                type,
                edit=false
            },
            state: {}
        } = this
        return !list.length ? <EmptyData className='collection_no_data' /> :
            <div className={`collection_list_component`}>
                {
                    list.map((e: any, i: number) => <CollectionItem edit={edit} type={type} key={i} index={i} collection={e} />)
                }
            </div>

    }

    componentDidMount(): void {
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default CollectionList
