import {mallApis} from 'apis'
import {initState as commonState} from 'reducer/common'
import * as commonActions from './common'
import {WxConfig} from 'utils'

const {goodsTypes} = commonState

export const Types = {
    getBanners: 'GET_BANNERS',
    getHomeGoods: "GET_HOME_GOODS",
    toggleGoodsTypes: 'TOGGLE_GOODS_TYPES',
    getGoodsList: 'GET_GOODS_LIST',
    changeMallData: 'CHANGE_MALL_DATA',
    getGoodsDetail: 'GET_GOODS_DETAIL',
    getAuctionScenes: 'GET_AUCTION_SCENES',
    getStoreList: 'GET_STORE_LIST'
}


const getBanners = () => async (dispatch: any) => {
    const list = await mallApis.getBanners()
    dispatch({
        type: Types.getBanners,
        payload: {
            list
        }
    })
}

const getHomeGoods = () => async (dispatch: any) => {
    const {list} = await mallApis.getHomeGoods()
    await dispatch({
        type: Types.getHomeGoods,
        payload: {
            list
        }
    })
}

const toggleGoodsTypes = () => async (dispatch: any) => {
    await dispatch({
        type: Types.toggleGoodsTypes
    })
}

const getGoodsList = (obj: any) => async (dispatch: any) => {
    if (obj.data.page === 1) {
        await dispatch({
            type: Types.changeMallData,
            payload: {
                goodsList: []
            }
        })
    }
    const {list = []} = await commonActions.getPaginationData(obj)(dispatch)
    await dispatch({
        type: Types.getGoodsList,
        payload: {
            list
        }
    })
}


const getStoreHome = (data: any) => async (dispatch: any) => {
    if (data.page === 1) {
        await dispatch({
            type: Types.changeMallData,
            payload: {
                goodsList: []
            }
        })
    }
    const {data: storeInfo, list} = await commonActions.getPaginationData({
        url: '/queryStoreHome',
        data
    })(dispatch)
    await dispatch({
        type: commonActions.Types.getStoreInfo,
        payload: {
            storeInfo
        }
    })
    await dispatch({
        type: Types.getGoodsList,
        payload: {
            list
        }
    })

    const {storeId, isAttention} = storeInfo
    if (isAttention === '1') {
        await doAttentionStore(storeId, 0, true)(dispatch)
    }
    return {shopList: list, storeInfo}
}

const changeMallData = (data: object) => async (dispatch: any) => {
    await dispatch({
        type: Types.changeMallData,
        payload: data
    })
}

const getGoodsDetail = (shopId: string, type: number, samePage?: boolean) => async (dispatch: any) => {
    // if (!samePage) {
    await commonActions.wxOAuth2()(dispatch)
    // }
    const {goods} = await mallApis.getGoodsDetail(goodsTypes[type - 1].detailapi, shopId)
    if (type === 4) {
        let {tariff = [], finishTime, alreadyPrice = 0, targetPrice} = goods
        tariff.sort((a: any, b: any) => a.price - b.price)
        tariff.map((e: any) => {
            e.str = e.describe.replace(',', '<数量<')
        })
        const h = (new Date(finishTime).getTime() - new Date().getTime()) / 1000 / 3600
        Object.assign(goods, {
            percent: Math.floor(alreadyPrice / targetPrice * 10000) / 100,
            leftDay: h < 0 ? 0 : Math.floor(h / 24),
            leftHour: h < 0 ? 0 : Math.ceil(h % 24),
            tariff,
            alreadyPrice,
            maxPrice: tariff.length ? tariff[tariff.length - 1].price : 0,
            minPrice: tariff.length ? tariff[0].price : 0
        })
    }
    await dispatch({
        type: Types.getGoodsDetail,
        payload: {
            goods
        }
    })
    const {shopName, shopPriImg, remark, explain} = goods
    await WxConfig.onShare({
        title: shopName,
        desc: remark || explain || shopName,
        imgUrl: shopPriImg[0].medium
    })
    return {...goods}
}

const doCollectionGoods = (shopId: string, shopType: number, status: number) => async (dispatch: any) => {
    const res = await mallApis.doCollectionGoods({shopId, shopType, status})
}

const doAttentionStore = (storeId: string, isAttention: number, doStatic = false) => async (dispatch: any) => {
    await mallApis.doAttentionStore({storeId, isAttention}, {doStatic})
}


const getAuctionScenes = (storeId: string) => async (dispatch: any) => {
    const {auctionScenes} = await mallApis.getAuctionScenes(storeId)
    await dispatch({
        type: Types.getAuctionScenes,
        payload: {
            auctionScenes
        }
    })
}

const getStoreList = (params: object) => async (dispatch: any) => {
    const {list, data} = await commonActions.getPaginationData(params)(dispatch)
    await dispatch({
        type: Types.getStoreList,
        payload: {
            list
        }
    })
}

export {
    getBanners,
    getHomeGoods,
    toggleGoodsTypes,
    getGoodsList,
    changeMallData,
    getGoodsDetail,
    doCollectionGoods,
    doAttentionStore,
    getStoreHome,
    getAuctionScenes,
    getStoreList
}
