import {Fetch} from 'utils'

export const getTreasureTypes = async () => {
    const {sayTypeList = []} = await Fetch('/sayTypeList', {})
    return {list: sayTypeList}
}

export const praiseTreasure = async (data: object) => {
    return await Fetch('/praiseSaySunTreasure', data, {needLogin: true})
}
export const collectTreasure = async (data: object) => {
    return await Fetch('/favoritePaintSunTreasure', data, {needLogin: true})
}
export const attentionTreasure = async (data: object) => {
    return await Fetch('/userAttentionSunTreasure', data, {needLogin: true})
}

export const getTreasureDetail = async (data: object) => {
    return await Fetch('/querySayDetailSunTreasure', data)
}
