import React, {ReactNode} from 'react'
import {Popover} from 'antd-mobile'
import {bindActionCreators} from 'redux'
import {entries} from "reducer";
import {commonActions} from "action";
import {connect} from "react-redux";
import {Utils} from 'utils'
import './index.scss'

interface P {
    className?: string,
    options: any[],
    optionProps?: any,
    name?: string,
    onChange?: any,
    children: ReactNode,
    props?: any,
    changeCommonData: any,
    formFields: any
}

interface S {
    visible: boolean
}


class VsonPopover extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {
            visible: false
        };
    }

    static defaultProps = {
        optionProps: {
            label: 'text',
            value: 'value'
        }
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                children,
                className = '',
                options,
                optionProps,
                props = {
                    placement: "bottom"
                },

            },
            state: {
                visible
            },
            onSelect
        } = this
        const items: ReactNode[] = []
        options.map(((e: any, i: number) => {
            let text = e, key = i;
            if (Utils.getDataType(e) === 'Object') {
                text = e[optionProps.label]
                key = e[optionProps.value]
            }
            items.push((<Popover.Item key={key}>{text}</Popover.Item>))
        }))
        return (
            <Popover visible={visible} overlay={items} {...props} onSelect={onSelect}>
                {children}
            </Popover>
        );
    }

    onSelect = async (e?: any, index?: number) => {
        const {
            onChange = () => {
            }, name = '', formFields, changeCommonData, options, optionProps
        } = this.props
        this.setState({visible: false})
        if (!!name) {
            formFields[name] = !!e ? e.key : options[0][optionProps.value]
            await changeCommonData({formFields: {...formFields}})
        }
        if (!!e) {
            onChange(e.key, options[index || 0])
        }
    }

    componentDidMount(): void {
        this.onSelect()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {formFields} = state.commonReducer
    return {
        formFields
    }
}

function mapDispatchToProps(dispatch: any) {
    const {changeCommonData} = commonActions
    return {
        ...bindActionCreators({changeCommonData}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(VsonPopover)
