import {asyncComponent} from 'components'

const MuseumHome = asyncComponent(() => import("./museum_home"));
const CollectionList = asyncComponent(() => import("./collection_list"));
const CollectionEdit = asyncComponent(() => import("./collection_edit"));
const CollectionDetail = asyncComponent(() => import("./collection_detail"));
const CollectionComments = asyncComponent(() => import("./collection_comments"));
const MuseumContact = asyncComponent(() => import("./museum_contact"));
const MuseumIntroduce = asyncComponent(() => import("./museum_introduce"));
const MuseumList = asyncComponent(() => import("./museum_list"));
const AdsInfoList = asyncComponent(() => import("./ads_info_list"));
const MuseumTypes = asyncComponent(() => import("./museum_types"));
const SetCash = asyncComponent(() => import("./set_cash"));
const MuseumTypeEdit = asyncComponent(() => import('./museum_type_edit'))
const MuseumSubTypes = asyncComponent(() => import('./museum_subtypes'))
const MuseumSubTypeEdit = asyncComponent(() => import('./museum_subtype_edit'))
const MuseumEdit = asyncComponent(() => import('./museum_edit'))
const AdsInfoDetail = asyncComponent(() => import('./ads_info_detail'))

const Museums = {
    museum_edit: {
        page: MuseumEdit,
        params: ''
    },
    ads_info_detail: {
        page: AdsInfoDetail,
        params: '/:id'
    },
    collection_list: {
        page: CollectionList,
        params: '/:by/:id?/:isMyself?/:typeId?/:museumId?/',
        bys: ['museum', 'subType', 'boutique', 'search', 'favorite']
    },
    collection_edit: {
        page: CollectionEdit,
        params: '/:id/:typeId?/:museumId?',
    },
    collection_detail: {
        page: CollectionDetail,
        params: '/:id/:fromWebview?'
    },
    collection_comments: {
        page: CollectionComments,
        params: '/:id'
    },
    museum_home: {
        page: MuseumHome,
        params: '/:id'
    },
    museum_contact: {
        page: MuseumContact,
        params: '/:id'
    },
    museum_introduce: {
        page: MuseumIntroduce,
        params: '/:id'
    },
    museum_list: {
        page: MuseumList,
        params: '/:by?'
    },
    ads_info_list: {
        page: AdsInfoList,
        params: '/:id'
    },
    museum_types: {
        page: MuseumTypes,
        params: '/:id/:isMyself?'
    },
    set_cash: {
        page: SetCash,
        params: '/:id'
    },
    museum_type_edit: {
        page: MuseumTypeEdit,
        params: '/:opr/:id'
    },
    museum_subtypes: {
        page: MuseumSubTypes,
        params: '/:museumId/:id/:name/:isMyself?'
    },
    museum_subtype_edit: {
        page: MuseumSubTypeEdit,
        params: '/:id/:museumId?'
    }
}
export default Museums
